@media all and (max-width:1070px) {

  .phoneList {
    background: white;
    width: 99%;
    margin: 50px auto;
    overflow: hidden;
  }
}

@media all and (max-width:810px) {

  .phoneList {
    background: white;
    width: 99%;
    margin: 50px auto;
    overflow: hidden;
  }
}

@media all and (max-width:725px) {

  .phoneList {
    background: white;
    width: 99%;
    margin: 50px auto;
    overflow: hidden;
  }

  .phoneID {
    display: none
  }

}

@media all and (max-width:445px) {

  .phoneList {
    background: white;
    width: 99%;
    margin: 50px auto;
    overflow: hidden;
  }

  .phoneID {
    display: none
  }

}


